<template>
    <div>
        <van-nav-bar
            title="我的收藏"
            left-text="返回" 
            @click-left="toback"
            :fixed="true"
            />
        <div class="collect-list clearfix">
            <div class="collect-one fl" v-for="(item,index) in collectData" :key="index" @click="goDetail(item.pid)">
                <div class="top">
                    <img class="auto-img listImg" :src="item.smallImg" alt="">
                </div>
                <div class="center">
                    {{item.name}}
                </div>
                <div class="bottom">
                    <div class="fl">￥{{item.price}}</div>
                    <div class="fr"><van-icon class="delete-icon" name="delete" @click.stop="noLike(item.pid)"/></div>
                    
                </div>
            </div>
        </div>
    </div>
</template>

<script>
    export default {
        data(){
            return{
                collectData:[]
            }
        },
        created(){
            this.getCollect();
        },
        methods:{
            toback(){
                this.$router.go(-1)
            },
            getCollect(){
                let tokenString = localStorage.getItem("login_Tk");

                this.$toast.loading({
                    message: "加载中...",
                    forbidClick: true,
                    duration: 0
                });

                this.axios({
                    method: "GET",
                    url: "/findAllLike",
                    params: {
                        appkey: this.appkey,
                        tokenString
                    }
                })
                .then(result => {
                    this.$toast.clear();
                    
                    if (result.data.code == 2000) {
                        this.collectData = result.data.result;
                    }
                })
                .catch(err => {
                    this.$toast.clear();
                    
                });
            },
            noLike(pid){
                let tokenString = localStorage.getItem('login_Tk');
                
                //开启加载提示
                this.$toast.loading({
                    message: "加载中...",
                    forbidClick: true,

                    //延迟自动关闭加载提示，如果该值为0，则不会自动关闭
                    duration: 0
                });
                this.axios({
                    method: 'POST',
                    url:'/notlike',
                    data: {
                        appkey: this.appkey,
                        pid: pid,
                        tokenString
                    }
                }).then(result => {
                    this.$toast.clear();
                    
                    if(result.data.code == 900){
                        this.getCollect();
                    }
                }).catch(err => {
                    this.$toast.clear();
                    
                }) 
            },
            goDetail(pid){
                this.$router.push({name:'Detail',query:{pid}})
            }
        }
    }
</script>

<style lang="less" scoped>
.collect-list{
    margin-top: 50px;
}
.collect-one{
    margin: 7px;
    padding: 10px;
    background-color:#f4b973;
    border-radius: 8px;
    .listImg{
        width: 150px;
        height: 150px;
    }
    .center{
        margin-left: 3px;
        margin-top: 5px;
        color:#a2459b;
    }
    .bottom{
        margin-top: 5px;
        >.fl{
            color:#a2459b ;
        }
        
        .delete-icon{
            font-size: 16px;
        }
    }
}

</style>